
.form-control {
	height: 40px;
	box-shadow: none;
	color: #969fa4;
}
.form-control:focus {
	border-color: #5cb85c;
}
.form-control, .btn {        
	border-radius: 3px;
}
.signup-form,.form-control,.btn {        
    border-radius: 3px;
}

.signup-form {
    width: 50%;
    margin: auto;
    padding: 30px 0;
    font-size: 15px;
}
.signup-form h2 {
	width: 40%;
	margin: 0 auto;
	padding: 30px 0;
  	font-size: 15px;
}
.signup-form h2 {
	color: #636363;
	position: relative;
	text-align: center;
}


.signup-form .hint-text {
	color: #999;
	margin-bottom: 30px;
	text-align: center;
}
.signup-form form {
	color: #999;
	border-radius: 3px;
	margin-bottom: 15px;
	background: #f2f3f7;
	box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
	padding: 30px;
}
.signup-form .form-group {
	margin-bottom: 20px;
}
.signup-form input[type="checkbox"] {
	margin-top: 3px;
}
.signup-form .btn {        
	font-size: 16px;
	font-weight: bold;		
	min-width: 140px;
	outline: none !important;
}
.signup-form .row div:first-child {
	padding-right: 10px;
}
.signup-form .row div:last-child {
	padding-left: 10px;
}    	
.signup-form a {
	color: #fff;
	text-decoration: underline;
}
.signup-form a:hover {
	text-decoration: none;
}
.signup-form form a {
	color: #5cb85c;
	text-decoration: none;
}	
.signup-form form a:hover {
	text-decoration: underline;
} 


@media only screen and (max-width: 767px){
	.signup-form {
		width: 80%;
		margin: 0 auto;
		padding: 20px 0;
		font-size: 15px;
	}

	.signup-form h2 {
		width: 40%;
		margin: auto;
		font-size: 20px;
	}
}


@media only screen and (max-width: 250px){
	.signup-form {
		width: 90%;
		margin: 0 auto;
		padding: 30px 0;
		font-size: 15px;
	}
.btn-info{
	font-size: 13px;
	margin-left: -20px;
}
}